.Hero{
    display: flex;
    height: 380px;
    background: linear-gradient(92.95deg, #C6F19E 10.38%, #95E465 10.4%, #C6F19E 10.41%, #95E465 44.96%, #C6F19E 82.28%, #9EE66F 96.45%);
}
.heading{
    align-items: flex-start;
    text-align: left;
    margin-left: 50px;
}
.pheading{
    color: #3f3d56;
    font-size: 50px;
    text-shadow: 2px 2px #626e65
}
.Image{
    align-items: flex-end;
    z-index: 1;
    margin-top: 10px;
    position: absolute;
    right: 0;
}
@media only screen and (max-width: 767px) {
    .pheading{
        font-size: 22px;
        text-align: left;
        text-shadow: none;
    }
    .Image{
        display: none;
    }
    .heading{
        height: auto;
        width: auto;
        margin: 0;
        text-align: left;
        padding: 20px;
    }
    .Hero{
        height: auto;
    }
}
@media only screen 
    and (min-device-width: 768px) 
    and (max-device-width: 1024px) 
    and (-webkit-min-device-pixel-ratio: 1) {
        .Hero{
            height: auto;
        }
        .heading{
            padding: 10px;
            
        }
        .Image{
            align-items: flex-end;
            z-index: 1;
            margin-top: 10px;
            position: absolute;
            right: 0;
            height: 160px;
        }
        .pheading{
            font-size: 2em;
            text-align: left;
            text-shadow: none;
        }
}